body {
    font-family: Montserrat, sans-serif;
    margin: 0;
    padding: 0;

    &.modal-open {
        overflow: hidden;
      }
}

/*      (a   .link */
a {
    text-decoration: none;
    color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

/*      (ul   .list) */
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

/*      */
img {
	display: block;
	max-width: 100%;
	height: auto;
}
/*      address */
address {
	font-style: normal;
}