
//   
.button {
    @include btn-order ();

    @include minW-Phone {
        padding: 20px auto;
    }

    @include minW-Tablet {
        width: 434px;
        font-size: 24px;
    }
}


//    ()
.button-modal {
    border-radius: 0px;
}


//    ( , ,  )
.button-service {
    @include btn-service ();

    @include minW-Phone {
        padding: 14px auto;
    }
}
//     HERO
.btn-hero {
    width: 232px;
    background-color: $button-opacity-bg;
}

//   
.arrows {
    display: flex;
    gap: 32px;

    &__button {
        width: 32px;
        height: 32px;
        padding: 0;
        border: none;
        background-color: transparent;
    
        &:hover,
        &:focus {
            .icon-arrow {
                fill: $accent-color;
            }
        }
    }
}

//    ()  HERO
.arrow-home {
    fill: $contrast-color;
}
